
@font-face {
  font-family: "Open Sans";
  src: url(/Brasirama/static/fonts/OpenSans-Regular.d7d5d45.ttf) format("truetype");
}
@font-face {
  font-family: "OpenSans-Extrabold";
  src: url(/Brasirama/static/fonts/OpenSans-ExtraBold.49f89e3.ttf) format("truetype");
}
@font-face {
  font-family: "OpenSans-Semibold";
  src: url(/Brasirama/static/fonts/OpenSans-SemiBold.e1c83f9.ttf) format("truetype");
}
html {
  background-color: #ffebe7;
  color: #201b53;
}
html, body, #app {
  margin: 0;
  padding: 0;
}
svg > * {
  font-weight: normal !important;
}
#app {
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 {
  font-family: OpenSans-Extrabold, Open Sans;
  font-weight: 700;
}
button:active {
  outline: none;
}
button:focus {
  outline: 0;
}
button::-moz-focus-inner {
  border: 0;
}
.cabecalho {
  width: 100vw;
  height: 50px;
  background: #201b53;
  padding: 5px;
}
.logo {
  width: 200px;
  margin: auto;
  display: block;
}
button {
  font-family: 'Open Sans', sans-serif;
  padding: 15px;
  margin: 0;
  width: 150px;
  height: 60px;
  text-transform: uppercase;
  font-size: 20px;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 2s;
  transition: opacity 2s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.flex-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  margin: 50px;
}
.flex-row div {
    max-width: 400px;
}
.text-center {
  text-align: center;
}

#main-view {
    margin: auto;
    display: block;
    max-width: 1000px;
}
#top-row {
    /* border-bottom: 1px solid #b8a9ae; */
    position: relative;
    margin-top: 100px;
}
.next-button {
    height: 80px;
    position:absolute;
    top: 50%;
    right: 0;
    z-index: 100;
    -webkit-transform: translate(0%, -50%);
            transform: translate(0%, -50%);
}
.imagem{
    width: 20%;
}

.slider {
    position: relative;
    max-width: 400px;
}
.slider-image{
    pointer-events: none;
}
.renda-ops-text {
    max-width: 400px;
    font-family: OpenSans-Extrabold;
}
.top-text {
    max-width: 400px;
}

* {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.escola-btn {
    margin: 10px;
    width: 300px;
    height: 75px;
    text-transform: uppercase;
    font-size: 10px;
    color: #201b53;
    padding: 15px;
    text-transform: uppercase;
    font-size: 14px;
    background: rgb(255, 163, 143);
    border: solid 0px rgb(255, 163, 143);
    -webkit-box-shadow: 0px 9px 0px #fd4d59;
            box-shadow: 0px 9px 0px #fd4d59;
}
.escola-disabled {
    pointer-events: none;
    padding-top:10px;
    background: #fd4d59;
    -webkit-box-shadow: 0px 0px 0px #fd4d59;
            box-shadow: 0px 0px 0px #fd4d59;
}

.cronometro {
  color: #fd4d59;
  font-family: OpenSans-Extrabold;
}
.bar {
  height: 70px;
  /* Not 30px because the 1px top-border brings it up to 30px to match #graph */
  background: #26efe0;
  border-top: 1px solid #fff;
  margin-bottom: 30px;
}
.bar p {
    position: absolute;
    text-align: center;
    width: 100%;
    margin: 0;
    line-height: 30px;
}
.graph {
  width: 100%;
  /* width and height are arbitrary, just make sure the #bar styles are changed accordingly */
  height: 70px;
  background: #e98155;
  border: 1px solid #fff;
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a8a8a8', endColorstr='#cccccc',GradientType=0 );
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.escolaridade {
  width: 33%;
}
.btn-formar {
  padding: 15px;
  margin: 0;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  text-transform: uppercase;
  font-size: 14px;
  color: #201b53;
  font-family: OpenSans-Extrabold;
  -webkit-transition: margin-top 0.3s ease, margin-left 0.3s ease, -webkit-box-shadow 0.3s ease;
  transition: margin-top 0.3s ease, margin-left 0.3s ease, -webkit-box-shadow 0.3s ease;
  transition: margin-top 0.3s ease, margin-left 0.3s ease, box-shadow 0.3s ease;
  transition: margin-top 0.3s ease, margin-left 0.3s ease, box-shadow 0.3s ease, -webkit-box-shadow 0.3s ease;
  background: #ff5732;
  border: 0;
  -webkit-box-shadow: 0px 9px 0px #201b53;
          box-shadow: 0px 9px 0px #201b53;
}
.btn-formar:active {
    -webkit-transition: margin-top 0.0s ease, -webkit-box-shadow 0.0s ease;
    transition: margin-top 0.0s ease, -webkit-box-shadow 0.0s ease;
    transition: margin-top 0.0s ease, box-shadow 0.0s ease;
    transition: margin-top 0.0s ease, box-shadow 0.0s ease, -webkit-box-shadow 0.0s ease;
    margin-top: 10px;
    -webkit-box-shadow: 0px 0px 0px #201b53;
            box-shadow: 0px 0px 0px #201b53;
}
























































.end-text {
  font-family: 'OpenSans-Extrabold', sans-serif;
  text-align: center;
  font-size: 30px;
  color: #201b53;
  margin: 70px;
}
.end-btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: #201b53;
  background: #ffa38f;
  width: auto;
  height: auto;
  border: none;
  padding: 15px;
  text-weight: bold;
  -webkit-box-shadow: 0px 5px 0px #201b53;
          box-shadow: 0px 5px 0px #201b53;
}



















* {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-weight: bold;
  color: #201b53;
  z-index: 100;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  background: #ffa38f;
  border: none;
  outline: none;
  -webkit-box-shadow: 0px 9px 0px #fd4d59;
          box-shadow: 0px 9px 0px #fd4d59;
}
.play-button:active {
    margin-top: 10px;
    -webkit-box-shadow: 0px 0px 0px #fd4d59;
            box-shadow: 0px 0px 0px #fd4d59;
}
.fullscreen {
  background-color: black;
  position: fixed;
  /* height: 100vh; */
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
}
